import React, { Fragment } from 'react';
import { connect } from 'react-redux';
import styles from './index.module.scss';
import modalStyle from './modal.module.scss';
import { AppStore } from '../../store/reducers';
import Title from './Title';
import CarInfo from './CarInfo';
import LogisticsInfo from './LogisticsInfo';
import PriceInfo from './PriceInfo';
import DepositAndPayment from './DepositAndPayment';
import AdditionalClauses from './AdditionalClauses';
import _get from '../../common/get';
import { post, get } from '../../api/http';
import { FormattedMessage } from 'react-intl';
import { Button } from 'antd';
import MakeOffer from './MakeOffer';
import { OFFER_STATUS } from './types';
import AlertModal from '../AlertModal';
import IconFont from '../ui/TradexIcon';
import { path } from '../../routes/routers';
import { RoomStatus } from '../../components/ChatMain/common';

interface BasePros {
  style?: object;
  className?: any;
  children?: any;
  roomType?: RoomStatus;
}
interface Props extends BasePros {
  result: object;
  chatroom: object;
}
type State = any;

class OfferBubble extends React.Component<Props, State> {
  constructor(props: Props) {
    super(props);
    this.state = {
      needShowLess: _get(this.props, ['result', 'extra', 'offer_status']) === -1,
      showMakeOffer: false,
      offerStatus: _get(this.props, ['result', 'extra', 'offer_status']),
      makeOfferData: {},
      isRenderModal: false,
      modalData: '',
      acceptButtonAvailble: true
    };
  }
  displayLess(isLess: boolean) {
    this.setState({ ...this.state, needShowLess: isLess });
  }
  isInvalidOfferBubble() {
    const offerStatus = _get(this.props, ['result', 'extra', 'offer_status']);
    return offerStatus === OFFER_STATUS.INVALID;
  }
  isDealDone() {
    return [
      OFFER_STATUS.ACCEPT,
      OFFER_STATUS.REVOKED,
      OFFER_STATUS.INVALID,
      OFFER_STATUS.DECLINE
    ].includes(this.state.offerStatus);
  }
  isSender(): boolean {
    const senderId = _get(this.props, ['result', 'sender_id']);
    const userId = _get(this.props, ['userInfo', 'userId']);
    return senderId === userId;
  }
  showOperation(): boolean {
    const isNotSender = !this.isSender();
    const isValid = !this.isInvalidOfferBubble();
    return isNotSender && isValid && !this.isDealDone();
  }
  showLessButton(): boolean {
    return this.isInvalidOfferBubble();
  }
  getPrice() {
    const items = _get(this.props, ['result', 'extra', 'color_options']);
    const item = (items.length && items[0]) || {};
    const msrp: number = _get(item, ['msrp']);
    const additionalFee: number = _get(item, ['additional_fee']);
    const currency: string = _get(item, ['currency']);
    const tax: number = _get(item, ['tax_per_car']);
    const shipmentFee: string = _get(item, ['shipment_fee']);
    const price: string = _get(item, ['price']);

    return {
      msrp,
      additionalFee,
      currency,
      tax,
      shipmentFee,
      price
    };
  }
  acceptOffer() {
    const offerID = _get(this.props, ['result', 'extra', 'offer_id']);
    const isNewCar = _get(this.props, ['chatroom', 'car_info', 'is_new_car']);
    const url = isNewCar ? `offers/${offerID}/accept/` : `used_car/offers/${offerID}/accept/`;
    if (this.state.acceptButtonAvailble) {
      this.setState({ ...this.state, acceptButtonAvailble: false }, () => {
        post(url, {})
          .then(() => {
            const offerStatus = OFFER_STATUS.ACCEPT;
            this.setState({ ...this.state, offerStatus, acceptButtonAvailble: true });
          })
          .catch(res => {
            this.setState({
              ...this.state,
              isRenderModal: true,
              modalData: _get(res, ['response', 'data', 'message']),
              acceptButtonAvailble: true
            });
          });
      });
    }
  }
  isSeller() {
    const userId = _get(this.props, ['userInfo', 'userId']);
    const sellerId = _get(this.props, ['result', 'extra', 'seller_id']);
    return userId === sellerId;
  }
  isChinaBuyer() {
    const buyerId = _get(this.props, ['result', 'extra', 'buyer_id']);
    const sender = _get(this.props, ['result', 'sender']);
    const receiver = _get(this.props, ['result', 'receiver']);
    const buyerInfo = [sender, receiver].find(item => item.id === buyerId);
    if (!buyerInfo) {
      return false;
    } else if (buyerInfo.company_country === 'CN') {
      return true;
    } else {
      return false;
    }
  }
  openMakeOffer(isSeller: boolean) {
    const garageId = _get(this.props, ['chatroom', 'car_info', 'id']);
    const priceData = this.getPrice();
    get(`garages/${garageId}/`).then((res: any) => {
      let transactionMethod = Number(_get(this.props, ['result', 'extra', 'transaction_method']));
      let orderId = _get(this.props, ['result', 'order_id']);
      let setting = _get(this.props, ['setting']);
      let paymentMethod = _get(this.props, ['result', 'extra', 'payment_method']);
      let { shipmentFee, ...restInfo } = this.getUsedCarInfo();
      let dest_port = {
        ..._get(this.props, ['result', 'extra', 'dest_port']),
        shipment_fee: shipmentFee
      };
      let detail = {
        ...res.data,
        ...restInfo,
        shipMethod: _get(this.props, ['result', 'extra', 'ship_method']),
        deposit_percentage: _get(this.props, ['result', 'extra', 'deposit_percentage']),
        dest_port,
        additional_statement: _get(this.props, ['result', 'extra', 'additional_clauses']),
        isSeller,
        price: priceData.price,
        tax: priceData.tax,
        additionalFee: priceData.additionalFee,
        msrp: priceData.msrp,
        addititonalText: '',
        is_new_car: _get(this.props, ['chatroom', 'car_info', 'is_new_car'])
      };
      let makeOfferData = {
        detail,
        transactionMethod,
        garageId,
        orderId,
        paymentMethod,
        isCN: this.isChinaBuyer(),
        setting
      };
      this.setState({ ...this.state, showMakeOffer: true, makeOfferData });
    });
  }
  counterOffer() {
    const isSeller = this.isSeller();
    this.openMakeOffer(isSeller);
  }
  sendNewOffer() {
    this.openMakeOffer(false);
  }
  revokedOffer() {
    const offerId = _get(this.props, ['result', 'extra', 'offer_id']);
    if (offerId) {
      post(`offers/${offerId}/revoke/`, {})
        .then(() => {
          const offerStatus = OFFER_STATUS.REVOKED;
          this.setState({ ...this.state, offerStatus });
        })
        .catch((e: any) => {
          const message = _get(e, ['response', 'data', 'detail']);
          if (message === 'Offer status is forbidden') {
            const receiverName = _get(this.props, ['result', 'receiver', 'display_name']);
            const values = { name: receiverName || '' };
            const modalData = (
              <FormattedMessage id="offerBubble_title_revokeModal_message" values={values} />
            );
            const newState = { ...this.state, isRenderModal: true, modalData };
            this.setState(newState);
          } else {
            const modalData = message;
            const newState = { ...this.state, isRenderModal: true, modalData };
            this.setState(newState);
          }
        });
    }
  }
  getUsedCarInfo() {
    const items = _get(this.props, ['result', 'extra', 'color_options']);
    if (items.length && items.length > 0) {
      return {
        currency: _get(items[0], ['currency']),
        available: _get(items[0], ['number_of_cars']),
        price: _get(items[0], ['price']),
        msrp: _get(items[0], ['msrp']),
        additionalFee: _get(items[0], ['additional_fee']),
        shipmentFee: _get(items[0], ['shipment_fee'])
      };
    } else {
      return {};
    }
  }
  closeMakeOffer(offerStatus: number | undefined) {
    if (offerStatus !== undefined && offerStatus !== null) {
      this.setState({ ...this.state, showMakeOffer: false, offerStatus });
    } else {
      this.setState({ ...this.state, showMakeOffer: false });
    }
  }
  componentDidUpdate(prevProps: any) {
    const prevOfferStatus = _get(prevProps, ['result', 'extra', 'offer_status']);
    const curOfferStatus = _get(this.props, ['result', 'extra', 'offer_status']);
    if (prevOfferStatus !== curOfferStatus) {
      const state = {
        needShowLess: _get(this.props, ['result', 'extra', 'offer_status']) === -1,
        offerStatus: _get(this.props, ['result', 'extra', 'offer_status'])
      };
      this.setState({ ...this.state, ...state });
    }
  }
  closeModal() {
    this.setState({
      ...this.state,
      isRenderModal: false,
      modalData: ''
    });
  }
  render() {
    const { style, className, roomType, ...newProps } = this.props;
    const { needShowLess, showMakeOffer, isRenderModal } = this.state;
    const width = roomType === 'max' ? '360px' : '340px';
    const modal = (
      <AlertModal>
        <div className={modalStyle.modal}>
          <div className={modalStyle.cancelIcon} onClick={this.closeModal.bind(this)}>
            <IconFont type="iconicon_cancel" />
          </div>
          <div className={modalStyle.content}>{this.state.modalData}</div>
          <div className={modalStyle.divider}></div>
          <div className={modalStyle.operation}>
            <Button className={modalStyle.button} onClick={this.closeModal.bind(this)}>
              <FormattedMessage id="offerBubble_title_revokeModal_confirm" />
            </Button>
          </div>
        </div>
      </AlertModal>
    );
    return (
      <div
        style={{ ...style, width }}
        className={`${styles.container} ${needShowLess ? styles.invaild : ''} ${className || ''}`}
      >
        <Title
          {...newProps}
          offerStatus={this.state.offerStatus}
          revokedOffer={this.revokedOffer.bind(this)}
          sendNewOffer={this.sendNewOffer.bind(this)}
          showMoreOperations={!this.showOperation()}
        />
        <CarInfo {...newProps} />
        <LogisticsInfo className={styles.logisticsInfo} {...newProps} />
        <PriceInfo {...newProps} isChinaBuyer={this.isChinaBuyer()} />
        {needShowLess ? (
          <div className={styles.moreDetails}>
            <Button className={styles.showMore} onClick={() => this.displayLess(false)}>
              <FormattedMessage id="offerBubble_operation_moreDetails" />
            </Button>
          </div>
        ) : (
          <Fragment>
            <DepositAndPayment {...newProps} isChinaBuyer={this.isChinaBuyer()} />
            <AdditionalClauses {...newProps} />
            {this.showOperation() ? (
              <div className={styles.operation}>
                <Button
                  disabled={this.isDealDone()}
                  className={`${styles.counterOffer} ${this.isDealDone() ? styles.disabled : ''}`}
                  onClick={() => this.counterOffer()}
                >
                  <FormattedMessage id="offerBubble_operation_counteroffer" />
                </Button>
                <Button
                  disabled={this.isDealDone()}
                  className={`${styles.accept} ${this.isDealDone() ? styles.disabled : ''}`}
                  onClick={() => this.acceptOffer()}
                >
                  <FormattedMessage id="offerBubble_operation_accept" />
                </Button>
              </div>
            ) : (
              ''
            )}
            {this.showLessButton() ? (
              <div className={styles.lessButton}>
                <Button className={styles.showLess} onClick={() => this.displayLess(true)}>
                  <FormattedMessage id="offerBubble_operation_showLess" />
                </Button>
              </div>
            ) : (
              ''
            )}
          </Fragment>
        )}
        {showMakeOffer ? (
          <MakeOffer
            onClose={(offerStatus: number | undefined) => this.closeMakeOffer(offerStatus)}
            {...this.state.makeOfferData}
          />
        ) : (
          ''
        )}
        {isRenderModal ? modal : ''}
      </div>
    );
  }
}

const mapStateToPros = (state: AppStore) => ({
  userInfo: state.user.userInfo,
  setting: state.setting
});

export default connect(mapStateToPros)(OfferBubble);
