import React from 'react';
import { FormattedMessage, FormattedNumber } from 'react-intl';
import _get from '../../../common/get';
import styles from './index.module.scss';

interface Props {
  style?: object;
  className?: object;
  result?: any;
  chatroom?: any;
}

class AdditionalClauses extends React.Component<Props> {
  constructor(props: Props) {
    super(props);
  }
  render() {
    const additionalClauses: Array<string> =
      _get(this.props, ['result', 'extra', 'additional_clauses']) || [];
    // flat
    const validAdditionalClauses = additionalClauses
      .flat()
      .filter((item: string) => item.trim() !== '');
    const shouldRender = validAdditionalClauses.length > 0;
    if (shouldRender) {
      return (
        <div className={`${styles.container} ${styles.font}`}>
          <div className={styles.outPanel}>
            <div className={styles.panel}>
              <p className={styles.title}>
                <FormattedMessage id="offerBubble_additionalClauses_title" />
              </p>
              {validAdditionalClauses.map((item: string, index: number) => (
                <p key={index} className={styles.content}>
                  {item}
                </p>
              ))}
            </div>
          </div>
        </div>
      );
    } else {
      return '';
    }
  }
}

export default AdditionalClauses;
