import React from 'react';
import { Row, Col } from 'antd';
import { CardWithoutTitle } from '../Card';
import { FormattedMessage, FormattedNumber } from 'react-intl';
import styles from './index.module.scss';

export const Detail = (props: any) => {
  let {
    type,
    tax,
    number,
    additionalFee,
    price,
    msrpPrice,
    addititonalText,
    paymentMethod,
    deposit,
    shippingFee,
    depositPercentage,
    currency,
    needRenderTax,
    isCNUser = false,
    ...newProps
  } = props;
  return (
    <CardWithoutTitle {...newProps}>
      <Col style={{ margin: '32px 0px 0px 20px' }}>
        {type === 'MSRP'
          ? renderMSRPDetail({ number, additionalFee, msrpPrice, currency })
          : type === 'CarPrice'
          ? renderCarPrice({ number, price, currency })
          : rederBuyNow({ number, price, currency })}
        <Row className={styles.detailRow} type="flex" justify="center" align="middle">
          <Col span={9} className={styles.detailItem}>
            <FormattedMessage id="makeOffer_shipping_shippingFee" defaultMessage="Shiping Fee" />
          </Col>
          <Col span={6} className={styles.price}>
            <FormattedNumber value={Number(number)} /> X
          </Col>
          <Col span={9} className={styles.price}>
            {isNaN(Number(shippingFee)) ? '--' : '$'}
            {isNaN(Number(shippingFee)) ? '' : <FormattedNumber value={Number(shippingFee)} />}
            {isNaN(Number(shippingFee)) ? '' : ` ${currency}`}
          </Col>
        </Row>
        {needRenderTax ? <TaxDetail number={number} tax={tax} currency={currency} /> : ''}
        <Row className={styles.detailRow} type="flex" justify="center" align="middle">
          <Col span={9} className={styles.detailItem}>
            {isCNUser ? (
              <FormattedMessage
                id="makeOffer_payment_securityDeposit"
                defaultMessage="Security Deposit"
              />
            ) : (
              <FormattedMessage id="makeOffer_payment_deposit" defaultMessage="Deposit" />
            )}
          </Col>
          <Col span={6} className={styles.price}>
            {`${depositPercentage}%`}
          </Col>
          <Col span={9} className={styles.price}>
            $<FormattedNumber value={Number(deposit)} /> {currency}
          </Col>
        </Row>
        <Row className={styles.detailRow} type="flex" justify="center" align="middle">
          <Col span={8} className={styles.detailItem}>
            <FormattedMessage
              id="makeOffer_payment_paymentMethod"
              defaultMessage="Payment method"
            />
          </Col>
          <Col span={16} className={styles.price}>
            {paymentMethod}
          </Col>
        </Row>
        {addititonalText.length > 0 && (
          <Row
            className={styles.detailRow}
            style={{ height: 88 }}
            type="flex"
            justify="center"
            align="top"
          >
            <Col span={8} className={styles.detailItem} style={{ height: '100%' }}>
              <FormattedMessage
                id="makeOffer_additionalClauses_title"
                defaultMessage="Additional clauses"
              />
            </Col>
            <Col
              span={16}
              className={styles.price}
              style={{
                height: '100%',
                wordWrap: 'break-word',
                overflow: 'hidden'
              }}
            >
              {addititonalText.join('\n')}
            </Col>
          </Row>
        )}
      </Col>
    </CardWithoutTitle>
  );
};
const renderMSRPDetail = (props: any) => {
  let { number, msrpPrice, additionalFee, currency } = props;
  return (
    <React.Fragment>
      <Row className={styles.detailRow} type="flex" justify="center" align="middle">
        <Col span={9} className={styles.detailItem}>
          <FormattedMessage id="makeOffer_carPrice_msrp" defaultMessage="MSRP" />
        </Col>
        <Col span={6} className={styles.price}>
          <FormattedNumber value={Number(number)} /> X
        </Col>
        <Col span={9} className={styles.price}>
          $<FormattedNumber value={Number(msrpPrice)} /> {currency}
        </Col>
      </Row>
      <Row className={styles.detailRow} type="flex" justify="center" align="middle">
        <Col span={9} className={styles.detailItem}>
          <FormattedMessage id="makeOffer_carPrice_additionalFee" defaultMessage="Additional fee" />
        </Col>
        <Col span={6} className={styles.price}>
          <FormattedNumber value={Number(number)} /> X
        </Col>
        <Col span={9} className={styles.price}>
          $<FormattedNumber value={Number(additionalFee)} /> {currency}
        </Col>
      </Row>
    </React.Fragment>
  );
};
const rederBuyNow = (props: any) => {
  let { number, price, currency } = props;
  return (
    <React.Fragment>
      <Row className={styles.detailRow} type="flex" justify="center" align="middle">
        <Col span={9} className={styles.detailItem}>
          <FormattedMessage id="makeOffer_detail_buyNow" defaultMessage="Buy Now" />
        </Col>
        <Col span={6} className={styles.price}>
          <FormattedNumber value={Number(number)} /> X
        </Col>
        <Col span={9} className={styles.price}>
          $<FormattedNumber value={Number(price)} /> {currency}
        </Col>
      </Row>
    </React.Fragment>
  );
};
const renderCarPrice = (props: any) => {
  let { number, price, currency } = props;
  return (
    <React.Fragment>
      <Row className={styles.detailRow} type="flex" justify="center" align="middle">
        <Col span={9} className={styles.detailItem}>
          <FormattedMessage id="makeOffer_detail_carPrice" defaultMessage="Car Price" />
        </Col>
        <Col span={6} className={styles.price}>
          <FormattedNumber value={Number(number)} /> X
        </Col>
        <Col span={9} className={styles.price}>
          $<FormattedNumber value={Number(price)} /> {currency}
        </Col>
      </Row>
    </React.Fragment>
  );
};

const TaxDetail = (props: any) => {
  let { number, tax, currency } = props;
  return (
    <React.Fragment>
      <Row className={styles.detailRow} type="flex" justify="center" align="middle">
        <Col span={9} className={styles.detailItem}>
          <FormattedMessage id="makeOffer_tax_name" defaultMessage="HST/GST/PST" />
        </Col>
        <Col span={6} className={styles.price}>
          <FormattedNumber value={Number(number)} /> X
        </Col>
        <Col span={9} className={styles.price}>
          $<FormattedNumber value={Number(tax)} /> {currency}
        </Col>
      </Row>
    </React.Fragment>
  );
};
