import React from 'react';
import { Row, Col } from 'antd';
import _get from '../../../common/get';
import styles from './index.module.scss';

interface BaseProps {
  className?: any;
  style?: React.CSSProperties;
  children?: any;
}
interface Props extends BaseProps {
  result: object;
  chatroom: object;
}
interface AddressProps extends BaseProps {
  country: string;
  city: string;
}

interface IconProps extends BaseProps {
  height?: number | string;
  width?: number | string;
}

const Container = ({ style, className, children }: BaseProps) => (
  <div style={style} className={`${styles.container} ${className} `}>
    {children}
  </div>
);

const Address = ({ country, city, className }: AddressProps) => (
  <div className={`${styles.address} ${className}`}>
    <div className={styles.city}>{city}</div>
    <div className={styles.country}>{country}</div>
  </div>
);

const Icon = ({ height, width, className, style }: IconProps) => (
  <div style={style} className={className}>
    <svg width={width || '26px'} height={height || '5px'} viewBox="0 0 26 5" version="1.1">
      <title>icon_right_chatbub_pc</title>
      <desc>Created with Sketch.</desc>
      <g id="" stroke="none" strokeWidth="1" fill="none" fillRule="evenodd">
        <g
          id="PC-18-Chat-Buy-Market-list"
          transform="translate(-874.000000, -953.000000)"
          fill="#12181F"
        >
          <g id="chat2" transform="translate(626.000000, 480.000000)">
            <g id="chat" transform="translate(20.000000, 146.000000)">
              <g id="offer-bubble1" transform="translate(71.000000, 156.000000)">
                <g id="">
                  <g id="1" transform="translate(16.000000, 132.000000)">
                    <g id="icon_right_chatbub_pc" transform="translate(141.000000, 39.000000)">
                      <polygon
                        id="Path"
                        points="25.8994487 5 0 5 2 3.5 22 3.5 18.5570893 0 20.9311577 0"
                      ></polygon>
                    </g>
                  </g>
                </g>
              </g>
            </g>
          </g>
        </g>
      </g>
    </svg>
  </div>
);

class LogisticsInfo extends React.Component<Props> {
  constructor(props: Props) {
    super(props);
  }
  getAddress(port: object): AddressProps {
    if (port) {
      return {
        city: _get(port, ['city_location', 'name', 'display_value']),
        country: _get(port, ['city_location', 'country', 'display_value'])
      };
    } else {
      return {
        city: '',
        country: ''
      };
    }
  }
  render() {
    const { className, style } = this.props;
    const originAddress = this.getAddress(_get(this.props, ['result', 'extra', 'warehouse']));
    const destAddress = this.getAddress(_get(this.props, ['result', 'extra', 'dest_port']));
    return (
      <Row
        style={style}
        className={`${styles.container} ${className}`}
        type="flex"
        justify="space-between"
        align="middle"
      >
        <Col span={8} className={styles.section}>
          <Address className={styles.origin} {...originAddress} />
        </Col>
        <Col span={8} className={styles.section}>
          <Icon className={styles.icon} width="26px" height="6px" />
        </Col>
        <Col span={8} className={styles.section}>
          <Address className={styles.dest} {...destAddress} />
        </Col>
      </Row>
    );
  }
}

export default LogisticsInfo;
