import React from 'react';
import { Row, Input } from 'antd';
import { Descriptions } from '../../../MakeOffer/Descriptions';
import { CardWithTitle } from '../Card';
import { FormattedMessage } from 'react-intl';
import styles from '../index.module.scss';
import styles2 from './index.module.scss';

type Props = any;
type State = any;
export class AdditionalClauses extends React.Component<Props, State> {
  constructor(props: Props) {
    super(props);
    this.state = {
      defaultStyle: {
        resize: 'none',
        fontFamily: 'SFProText',
        fontSize: 14,
        lineHeight: 1.5,
        color: '#8a9199',
        backgroundColor: '#f7f7f7'
      },
      style: {
        resize: 'none',
        fontFamily: 'SFProText',
        fontSize: 14,
        lineHeight: 1.5,
        color: '#8a9199',
        backgroundColor: '#f7f7f7'
      },
      onFocusStyle: {
        color: '#001a33',
        fontWeight: 500
      },
      defaultValue: 'Optional, input clauses'
    };
  }
  onFocus() {
    let { defaultStyle, onFocusStyle } = this.state;
    if (!this.props.value) {
      let style = { ...defaultStyle, ...onFocusStyle };
      this.setState({ ...this.state, style, defaultValue: '' });
    }
  }
  onBlur() {
    let { defaultStyle } = this.state;
    if (!this.props.value) {
      let style = { ...defaultStyle };
      this.setState({
        ...this.state,
        style,
        defaultValue: 'Optional, input clauses'
      });
    }
  }

  title = (
    <div className={styles2.title}>
      <FormattedMessage
        id="makeOffer_additionalClauses_title"
        defaultMessage="Additional Clauses"
      />
    </div>
  );

  render() {
    let { defaultValue, style } = this.state;
    let { additionalChange, className = '', ...restProps } = this.props;
    return (
      <Descriptions className={className} title={this.title}>
        <Descriptions.Row className={styles.row}>
          <Input.TextArea
            style={style}
            placeholder={defaultValue}
            value={this.props.value.join('\n')}
            onFocus={() => this.onFocus()}
            onBlur={() => this.onBlur()}
            onChange={(ev: any) => additionalChange(ev.target.value)}
            rows={3}
          />
        </Descriptions.Row>
      </Descriptions>
    );
  }
}
