import React from 'react';
import { FormattedMessage, FormattedNumber } from 'react-intl';
import { FormattedPrice } from '../../../FormattedPrice';
import { Descriptions } from '../../../MakeOffer/Descriptions';
import styles from './index.module.scss';

export const Tax = (props: any) => {
  let { style, className = '', number, tax, currency } = props;
  let value: any = '';

  const title = (
    <div className={`${styles.title}`}>
      <FormattedMessage id="makeOffer_tax_title" defaultMessage="Tax" />
    </div>
  );

  if (tax === '--') {
    value = tax;
  } else {
    value = (
      <React.Fragment>
        <FormattedPrice value={Number(tax)} currency={currency} />
        {` * `}
        <FormattedNumber value={Number(number)} />
      </React.Fragment>
    );
  }

  return (
    <Descriptions style={style} className={className} title={title}>
      <Descriptions.Row>
        <div className={styles.name}>
          <FormattedMessage id="makeOffer_tax_name" defaultMessage="HST/GST/PST" />
        </div>
        <div className={styles.value}>{value}</div>
      </Descriptions.Row>
    </Descriptions>
  );
};
