import React from 'react';
import { Row, Col } from 'antd';
import { CardWithoutTitle } from '../Card';
import TradexIcon from '../../../ui/TradexIcon';
import styles from './index.module.scss';

export const LogisticsInfo = (props: any) => {
  let { shipMethod, from, to, ...restProps } = props;
  const From = (pros: any) => {
    return (
      <div className={styles.horizontalLayout}>
        <TradexIcon type="iconicon_port_line" className="port_line" style={{ marginRight: 16 }} />
        <div className={styles.verticalLayout}>
          <div className={styles.cityName}>{from.city['display_value']}</div>
          <div className={styles.countryName}>{getShipMethod(shipMethod)}</div>
        </div>
      </div>
    );
  };
  const To = (props: any) => {
    return (
      <div className={styles.horizontalLayout}>
        <div className={styles.verticalLayout}>
          <div className={styles.cityName}>{to.city['display_value']}</div>
          <div className={styles.countryName}>{to.country['display_value']}</div>
        </div>
        <TradexIcon type="iconicon_port_line" className="port_line" style={{ marginLeft: 16 }} />
      </div>
    );
  };
  return (
    <CardWithoutTitle {...restProps}>
      <Row className={styles.logisticsInfo} type="flex" justify="center" align="middle">
        <Col span={8}>
          <From></From>
        </Col>
        <Col span={8} style={{ display: 'flex' }} className={styles.horizontalLayout}>
          <img
            className={styles.longrightarrow}
            src={require('../../../../assets/img/icon_longrightarrow@2x.png')}
            alt=""
          />
        </Col>
        <Col span={8}>
          <To></To>
        </Col>
      </Row>
    </CardWithoutTitle>
  );
};

function getShipMethod(code: number): string {
  let shipMethod = '';
  switch (code) {
    case 1:
      shipMethod = 'Own shipper';
      break;
    case 2:
      shipMethod = 'TRADE X';
      break;
  }
  return shipMethod;
}
