type Props = any;
type State = any;

interface BaseProps {
  style?: React.CSSProperties;
  className?: any;
  children?: any;
}
interface TotalPriceSectionProps {
  totalPrice: number;
  totalSellerPrice: number;
  totalTax: number;
  totalShippingFee: number;
  currency: string;
  shouldRenderTax: boolean;
  isRenderTotalSection: boolean;
}

const Container = ({ style, className = '', children }: BaseProps) => (
  <div style={style} className={`${styles.container} ${className}`}>
    {children}
  </div>
);

const BuyNowSection = (props: any) => {
  const {
    numberOfCars,
    price,
    currency,
    tax,
    shipmentFee,
    mileage,
    distanceUnit,
    pricePreCar,
    shouldRenderTax
  } = props;
  return (
    <div className={styles.priceSection}>
      <div className={styles.head}>
        <div className={styles.mileage}>
          <IconFont className={styles.iconFont} type="iconicon_mileage_area" />
          {`${mileage}${distanceUnit}`}
        </div>
        <div className={styles.numberAndTotalPrice}>
          <div className={styles.number}>{`${numberOfCars} `}</div>
          <div className={styles.totalPrice}>
            <FormattedPrice {...{ value: toNumber(pricePreCar), currency }} />
          </div>
        </div>
      </div>
      <div className={styles.priceItem}>
        <div className={styles.itemName}>
          <FormattedMessage id="offerBubble_priceInfo_buyNow" />
        </div>
        <div className={styles.itemValue}>
          <FormattedPrice {...{ value: toNumber(price), currency }} />
        </div>
      </div>
      {shouldRenderTax ? (
        <div className={styles.priceItem}>
          <div className={styles.itemName}>
            <FormattedMessage id="offerBubble_priceInfo_tax" />
          </div>
          <div className={styles.itemValue}>
            <FormattedPrice {...{ value: toNumber(tax), currency }} />
          </div>
        </div>
      ) : (
        ''
      )}
      <div className={styles.priceItem}>
        <div className={styles.itemName}>
          <FormattedMessage id="offerBubble_priceInfo_shippingFee" />
        </div>
        <div className={styles.itemValue}>
          {shipmentFee === 'Quote' ? (
            'Quote'
          ) : (
            <FormattedPrice {...{ value: toNumber(shipmentFee), currency }} />
          )}
        </div>
      </div>
    </div>
  );
};
