const TotalPriceSection = (props: TotalPriceSectionProps) => {
  const {
    totalPrice,
    totalSellerPrice,
    totalTax,
    totalShippingFee,
    currency,
    isRenderTotalSection,
    shouldRenderTax
  } = props;
  return (
    <div className={styles.totalPriceSection}>
      <div className={`${styles.item} ${styles.head}`}>
        <div className={styles.itemName}>
          <FormattedMessage id="offerBubble_priceInfo_inclShipping" />
        </div>
        <div className={`${styles.itemValue} ${styles.totalPrice}`}>
          <FormattedPrice {...{ value: totalPrice, currency }} />
        </div>
      </div>
      {isRenderTotalSection ? (
        <React.Fragment>
          <div className={styles.item}>
            <div className={styles.itemName}>
              <FormattedMessage id="offerBubble_priceInfo_totalSellerPrice" />
            </div>
            <div className={styles.itemValue}>
              <FormattedPrice {...{ value: totalSellerPrice, currency }} />
            </div>
          </div>
          {shouldRenderTax ? (
            <div className={styles.item}>
              <div className={styles.itemName}>
                <FormattedMessage id="offerBubble_priceInfo_totalTax" />
              </div>
              <div className={styles.itemValue}>
                <FormattedPrice {...{ value: totalTax, currency }} />
              </div>
            </div>
          ) : (
            ''
          )}
          <div className={styles.item}>
            <div className={styles.itemName}>
              <FormattedMessage id="offerBubble_priceInfo_totalShippingFee" />
            </div>
            <div className={styles.itemValue}>
              <FormattedPrice {...{ value: totalShippingFee, currency }} />
            </div>
          </div>
        </React.Fragment>
      ) : (
        ''
      )}
    </div>
  );
};
const toNumber = (vlaue: string | number): number => {
  let result = Number(vlaue);
  if (isNaN(result)) {
    return 0;
  } else {
    return result;
  }
};
class PriceInfo extends React.Component<Props, State> {
  constructor(props: Props) {
    super(props);
  }
  getTransactionMethod(props: Props): string {
    return _get(props, ['result', 'extra', 'transaction_method']);
  }
  shouldRenderTotalSection() {
    const items = _get(this.props, ['result', 'extra', 'color_options']);
    const totalCars = items
      .map((item: any) => _get(item, ['number_of_cars']))
      .reduce((cur: number, pre: number) => cur + pre, 0);
    return totalCars > 1;
  }
  isCA2CA() {
    try {
      const fromKey = _get(this.props, [
        'result',
        'extra',
        'warehouse',
        'city_location',
        'country',
        'key'
      ]);
      const toKey = _get(this.props, [
        'result',
        'extra',
        'dest_port',
        'city_location',
        'country',
        'key'
      ]);
      return fromKey === 'CA' && toKey === 'CA';
    } catch (e) {
      return false;
    }
  }
  getBuyNowPriceData() {
    const item =
      this.props &&
      this.props['result'] &&
      this.props['result']['extra'] &&
      this.props['result']['extra']['color_options'] &&
      this.props['result']['extra']['color_options'][0];

    const numberOfCars: number = _get(item, ['number_of_cars']);
    const price: string = _get(item, ['price']);
    const currency: string = _get(item, ['currency']);
    const tax: number = toNumber(_get(item, ['tax_per_car']));
    const shipmentFee: string = _get(item, ['shipment_fee']);
    const mileage: number = _get(this.props, ['chatroom', 'car_info', 'mileage']);
    const distanceUnit: string = _get(this.props, [
      'chatroom',
      'car_info',
      'distance_unit',
      'display_value'
    ]);
    const pricePreCar = toNumber(price) + toNumber(tax) + toNumber(shipmentFee);
    const shouldRenderTax = this.isCA2CA();

    return {
      numberOfCars,
      price,
      currency,
      tax,
      shipmentFee,
      mileage,
      distanceUnit,
      pricePreCar,
      shouldRenderTax
    };
  }
  getCarPriceData() {
    const item =
      this.props &&
      this.props['result'] &&
      this.props['result']['extra'] &&
      this.props['result']['extra']['color_options'] &&
      this.props['result']['extra']['color_options'][0];

    const numberOfCars: number = _get(item, ['number_of_cars']);
    const price: string = _get(item, ['price']);
    const currency: string = _get(item, ['currency']);
    const tax: number = toNumber(_get(item, ['tax_per_car']));
    const shipmentFee: string = _get(item, ['shipment_fee']);
    const mileage: number = _get(this.props, ['chatroom', 'car_info', 'mileage']);
    const distanceUnit: string = _get(this.props, [
      'chatroom',
      'car_info',
      'distance_unit',
      'display_value'
    ]);
    const pricePreCar: number = toNumber(price) + toNumber(tax) + toNumber(shipmentFee);
    const shouldRenderTax = this.isCA2CA();

    return {
      numberOfCars,
      price,
      currency,
      tax,
      shipmentFee,
      mileage,
      distanceUnit,
      pricePreCar,
      shouldRenderTax
    };
  }
  getMSRPPriceData() {
    const item =
      this.props &&
      this.props['result'] &&
      this.props['result']['extra'] &&
      this.props['result']['extra']['color_options'] &&
      this.props['result']['extra']['color_options'][0];

    const numberOfCars: number = _get(item, ['number_of_cars']);
    const msrp: string = _get(item, ['msrp']);
    const additionalFee: number = _get(item, ['additional_fee']);
    const currency: string = _get(item, ['currency']);
    const tax: number = toNumber(_get(item, ['tax_per_car']));
    const shipmentFee: string = _get(item, ['shipment_fee']);
    const mileage: number = _get(this.props, ['chatroom', 'car_info', 'mileage']);
    const distanceUnit: string = _get(this.props, [
      'chatroom',
      'car_info',
      'distance_unit',
      'display_value'
    ]);
    const carPrice = toNumber(msrp) + toNumber(additionalFee);
    const pricePreCar =
      toNumber(msrp) + toNumber(additionalFee) + toNumber(tax) + toNumber(shipmentFee);
    const shouldRenderTax = this.isCA2CA();
    return {
      numberOfCars,
      msrp,
      additionalFee,
      currency,
      tax,
      shipmentFee,
      mileage,
      distanceUnit,
      carPrice,
      pricePreCar,
      shouldRenderTax
    };
  }
  getTotalBuyNowPriceData() {
    const priceData = this.getBuyNowPriceData();
    const numberOfCars = priceData.numberOfCars;
    const totalPrice = priceData.pricePreCar * numberOfCars;
    const totalSellerPrice = toNumber(priceData.price) * numberOfCars;
    const totalTax = priceData.tax * numberOfCars;
    const totalShippingFee = toNumber(priceData.shipmentFee) * numberOfCars;
    const currency = priceData.currency;
    return { totalPrice, totalSellerPrice, totalTax, totalShippingFee, currency };
  }
  getTotalCarPriceData() {
    const priceData = this.getCarPriceData();
    const numberOfCars = priceData.numberOfCars;
    const totalPrice = priceData.pricePreCar * numberOfCars;
    const totalSellerPrice = toNumber(priceData.price) * numberOfCars;
    const totalTax = priceData.tax * numberOfCars;
    const totalShippingFee = toNumber(priceData.shipmentFee) * numberOfCars;
    const currency = priceData.currency;
    return { totalPrice, totalSellerPrice, totalTax, totalShippingFee, currency };
  }
  getTotalMSRPPriceData() {
    const priceData = this.getMSRPPriceData();
    const numberOfCars = priceData.numberOfCars;
    const totalPrice = priceData.pricePreCar * numberOfCars;
    const totalSellerPrice = priceData.carPrice * numberOfCars;
    const totalTax = priceData.tax * numberOfCars;
    const totalShippingFee = toNumber(priceData.shipmentFee) * numberOfCars;
    const currency = priceData.currency;
    return { totalPrice, totalSellerPrice, totalTax, totalShippingFee, currency };
  }
  renderBuyNowSection() {
    const priceData = this.getBuyNowPriceData();
    return <BuyNowSection {...priceData} />;
  }
  renderCarPriceSection() {
    const priceData = this.getCarPriceData();
    return <CarPriceSection {...priceData} />;
  }
  renderMSRPSection() {
    const priceData = this.getMSRPPriceData();
    return <MSRPSection {...priceData} />;
  }
  renderPriceSection() {
    const transactionMethod = this.getTransactionMethod(this.props);
    if (transactionMethod === TRANSACTION_METHOD.BUY_NOW) {
      return this.renderBuyNowSection();
    } else {
      if (this.props.isChinaBuyer) {
        return this.renderMSRPSection();
      } else {
        return this.renderCarPriceSection();
      }
    }
  }
  renderTotalPriceSection() {
    const transactionMethod = this.getTransactionMethod(this.props);
    const shouldRenderTax = this.isCA2CA();
    const isRenderTotalSection: boolean = this.shouldRenderTotalSection();
    if (transactionMethod === TRANSACTION_METHOD.BUY_NOW) {
      return (
        <TotalPriceSection
          {...{ ...this.getTotalBuyNowPriceData(), shouldRenderTax, isRenderTotalSection }}
        />
      );
    } else {
      if (this.props.isChinaBuyer) {
        return (
          <TotalPriceSection
            {...{ ...this.getTotalMSRPPriceData(), shouldRenderTax, isRenderTotalSection }}
          />
        );
      } else {
        return (
          <TotalPriceSection
            {...{ ...this.getTotalCarPriceData(), shouldRenderTax, isRenderTotalSection }}
          />
        );
      }
    }
  }
  render() {
    const { className = '', style } = this.props;
    return (
      <Container style={style} className={`${styles.font} ${className}`}>
        {this.renderPriceSection()}
        {this.renderTotalPriceSection()}
      </Container>
    );
  }
}

export default PriceInfo;
