import React from 'react';
import { Col } from 'antd';
import styles from './index.module.scss';

export default (props: any) => {
  let { children, needDivider = true, ...newProps } = props;
  return (
    <Col {...newProps}>
      {children}
      {needDivider ? <div className={styles.dashedDivider}></div> : false}
    </Col>
  );
};
