import React from 'react';
import { Row, Col, Button } from 'antd';
import { CardWithoutTitle } from '../Card';
import { FormattedMessage, FormattedNumber } from 'react-intl';
import styles from '../index.module.scss';

const styleButton = {
  width: 300,
  height: 64,
  borderRadius: 8,
  backgroundColor: '#3acdfe',
  fontSize: '24px',
  color: '#ffffff'
};

export const Total = (props: any) => {
  let { submit, total, transactionMethod, currency, ...restProps } = props;
  return (
    <CardWithoutTitle {...restProps}>
      <Row type="flex" justify="center" align="middle" style={{ margin: '32px 0px 48px 0px' }}>
        <Col span={8} className={styles.cardHead}>
          <FormattedMessage id="makeOffer_total_total" defaultMessage="Total" />
        </Col>
        <Col span={16} className={styles.total}>
          {isNaN(Number(total)) ? '--' : '$'}
          {isNaN(Number(total)) ? '' : <FormattedNumber value={Number(total)} />}
          {isNaN(Number(total)) ? '' : ` ${currency}`}
        </Col>
      </Row>
      <Row type="flex" justify="center" align="middle">
        <Button style={styleButton} className={styles.button} onClick={() => submit()}>
          <span>
            {transactionMethod === 1 ? (
              <FormattedMessage
                id="makeOffer_total_button_makeAnOffer"
                defaultMessage="Purchase Now"
              />
            ) : (
              <FormattedMessage
                id="makeOffer_total_button_buyNowPrice"
                defaultMessage="Send My Offer"
              />
            )}
          </span>
        </Button>
      </Row>
    </CardWithoutTitle>
  );
};
