import React from 'react';
import _get from '../../../common/get';
import styles from './index.module.scss';
import { FormattedMessage } from 'react-intl';

interface BaseProps {
  style?: React.CSSProperties;
  className?: any;
  children?: any;
}

interface Props extends BaseProps {
  result: object;
  chatroom: object;
}

const QualityTag = ({ quality }: { quality: string | number }) => {
  const messageId = quality > 1 ? 'offerBubble_carInfo_units' : 'offerBubble_carInfo_unit';
  return (
    <div className={`${styles.tag} ${styles.qualityTag}`}>
      {`${quality} `}
      <FormattedMessage id={messageId} />
    </div>
  );
};

const InStockTag = () => (
  <div className={`${styles.tag} ${styles.inStockTag}`}>
    <FormattedMessage id="offerBubble_carInfo_instock" />
  </div>
);

const IncomingTag = () => (
  <div className={`${styles.tag} ${styles.incomingTag}`}>
    <FormattedMessage id="offerBubble_carInfo_incoming" />
  </div>
);

class CarInfo extends React.Component<Props> {
  constructor(props: Props) {
    super(props);
  }
  getThumbnail(props: Props): string {
    return _get(props, ['chatroom', 'car_info', 'thumbnail']);
  }
  getName(props: Props): string {
    return _get(props, ['chatroom', 'car_info', 'name']);
  }
  render() {
    const isIncoming = _get(this.props, ['chatroom', 'car_info', 'car_status']) === 2;
    const isInstock = _get(this.props, ['chatroom', 'car_info', 'car_status']) === 1;
    const available: string | number = _get(this.props, ['chatroom', 'car_info', 'available']);
    return (
      <div className={styles.container}>
        <div className={styles.imgPanel}>
          <img src={`${this.getThumbnail(this.props)}?x-oss-process=style/web_640`} />
        </div>
        <div className={styles.infoPanel}>
          <div className={styles.tagSection}>
            {isIncoming ? <IncomingTag /> : ''}
            {isInstock ? <InStockTag /> : ''}
            <QualityTag quality={available} />
          </div>
          <div className={styles.infoSection}>{this.getName(this.props)}</div>
        </div>
      </div>
    );
  }
}

export default CarInfo;
