import React from 'react';
import { FormattedMessage, FormattedNumber } from 'react-intl';
import styles from './index.module.scss';
import _get from '../../../common/get';

interface Props {
  style?: React.CSSProperties;
  className?: any;
  result: any;
  chatroom: any;
  isChinaBuyer?: boolean;
}
const paymentMap = new Map([[1, 'TT'], [2, 'LC'], [3, 'EFT'], [4, 'T/T']]);

class DepositAndPayment extends React.Component<Props> {
  constructor(props: Props) {
    super(props);
  }
  render() {
    const paymentMethod = _get(this.props, ['result', 'extra', 'payment_method']);
    const depositPercentage = _get(this.props, ['result', 'extra', 'deposit_percentage']);
    const deposit = _get(this.props, ['result', 'extra', 'deposit']);
    const currency = _get(this.props, ['result', 'extra', 'seller_currency']);
    const isChinaBuyer = _get(this.props, ['isChinaBuyer']) || false;
    return (
      <div className={`${styles.container} ${styles.font}`}>
        <div className={styles.item}>
          <div className={styles.itemName}>
            <FormattedMessage
              id={
                isChinaBuyer
                  ? 'offerBubble_depositAndPayment_securityDeposit'
                  : 'offerBubble_depositAndPayment_deposit'
              }
            />
          </div>
          <div className={styles.itemValue}>{`${depositPercentage}%($${deposit} ${currency})`}</div>
        </div>
        <div className={styles.item}>
          <div className={styles.itemName}>
            <FormattedMessage id="offerBubble_depositAndPayment_payment" />
          </div>
          <div className={styles.itemValue}>{paymentMap.get(paymentMethod)}</div>
        </div>
      </div>
    );
  }
}

export default DepositAndPayment;
