import React from 'react';
import { Popover } from 'antd';
import styles from './index.module.scss';
import _get from '../../../common/get';
import IconFont from '../../ui/TradexIcon';
import { OFFER_STATUS } from '../types';

type Props = any;
type State = {
  showPopover: boolean;
};

class Title extends React.Component<Props, State> {
  constructor(props: Props) {
    super(props);
    this.state = {
      showPopover: false
    };
  }
  sendNewOffer = () => {
    this.setState({ ...this.state, showPopover: false });
    this.props.sendNewOffer && this.props.sendNewOffer();
  };
  revokedOffer = () => {
    this.setState(
      { ...this.state, showPopover: false },
      () => this.props.revokedOffer && this.props.revokedOffer()
    );
  };
  getRole(): string | void {
    const userId = _get(this.props, ['userInfo', 'userId']);
    const senderId = _get(this.props, ['result', 'sender_id']);
    const receiverId = _get(this.props, ['result', 'receiver_id']);
    if (userId === senderId) {
      return 'sender';
    } else if (userId === receiverId) {
      return 'receiver';
    }
  }
  getTitle(role: string): string | undefined {
    if (role === 'sender') {
      return _get(this.props, ['result', 'extra', 'content_sender']);
    } else if (role === 'receiver') {
      return _get(this.props, ['result', 'extra', 'content']);
    }
  }
  isValidOfferBubble() {
    return ![OFFER_STATUS.INVALID, OFFER_STATUS.REVOKED].includes(this.props.offerStatus);
  }
  renderMoreOperation(operations: Array<any>) {
    const content = (
      <div className={styles.moreOptions}>
        <div className={styles.item} onClick={this.sendNewOffer}>
          {operations && operations[0] && operations[0]['text']}
        </div>
        <div className={styles.divider}></div>
        <div className={styles.item} onClick={this.revokedOffer}>
          {operations && operations[1] && operations[1]['text']}
        </div>
      </div>
    );
    const popover = (
      <Popover
        arrowPointAtCenter
        placement="bottomRight"
        content={content}
        visible={this.state.showPopover}
      >
        <div
          className={styles.moreButton}
          onClick={() => this.setState({ ...this.state, showPopover: true })}
        >
          <IconFont type="iconicon_more_vertical" />
        </div>
      </Popover>
    );
    return popover;
  }
  render() {
    const role = this.getRole();
    const title = this.getTitle(role as string);
    const { className = '', style, showMoreOperations = false } = this.props;
    const operations = _get(this.props, ['result', 'extra', 'additional_opts']) || [];
    return (
      <div
        style={style}
        className={`${styles.container} ${
          this.isValidOfferBubble() ? '' : styles.invaild
        } ${className}`}
      >
        <div className={operations.length > 1 ? styles.title : styles.longTitle}>{title}</div>
        {operations.length > 1 && showMoreOperations ? this.renderMoreOperation(operations) : ''}
      </div>
    );
  }
}

export default Title;
