import React from 'react';
import { Descriptions } from '../../../MakeOffer/Descriptions';
import { FormattedPrice } from '../../../FormattedPrice';
import { InputNumber } from '../InputNumber';
import { PaymentMethodSelect, PaymentMethod } from '../../../Select/PaymentMethodSelect';
import { FormattedMessage, FormattedNumber } from 'react-intl';
import styles from './index.module.scss';

type PaymentProps = {
  paymentMethods: Array<PaymentMethod>;
  selectedPaymentMethod: PaymentMethod;
  selectChanged: any;
  percentageChange: any;
  deposit: any;
  currency: any;
  step: any;
  depositPercentage: any;
  isCNUser?: any;
  style?: any;
  className?: any;
};

export const Payment = (props: PaymentProps) => {
  let {
    style,
    className = '',
    paymentMethods,
    selectChanged,
    percentageChange,
    deposit,
    currency,
    step,
    depositPercentage,
    isCNUser = false,
    ...restProps
  } = props;

  const title = (
    <div className={styles.title}>
      <FormattedMessage id="makeOffer_payment_title" defaultMessage="Payment" />
    </div>
  );

  return (
    <Descriptions style={style} className={className} title={title}>
      <Descriptions.Row>
        <div className={styles.name}>
          <FormattedMessage
            id="makeOffer_payment_percentageOfDeposit"
            defaultMessage="Percentage of deposit"
          />
        </div>
        <div className={styles.value}>
          <InputNumber
            onChange={percentageChange}
            step={step}
            defaultValue={depositPercentage}
            fromatValue={(value: string) => `${value}%`}
            max={100}
          />
        </div>
      </Descriptions.Row>
      <Descriptions.Row>
        <div className={styles.name}>
          <FormattedMessage id="makeOffer_payment_paymentMethod" defaultMessage="Payment method" />
        </div>
        <div className={styles.value}>{renderPaymentMethods(props)}</div>
      </Descriptions.Row>
      <Descriptions.Row>
        <div className={styles.name}>
          {isCNUser ? (
            <FormattedMessage
              id="makeOffer_payment_securityDeposit"
              defaultMessage="Security Deposit"
            />
          ) : (
            <FormattedMessage id="makeOffer_payment_deposit" defaultMessage="Deposit" />
          )}
        </div>
        <div className={styles.value}>
          <FormattedPrice value={Number(deposit)} currency={currency} />
        </div>
      </Descriptions.Row>
    </Descriptions>
  );
};

function renderPaymentMethods(props: PaymentProps) {
  if (props.paymentMethods.length > 1) {
    return renderList(props);
  } else {
    return props.paymentMethods[0].name;
  }
}

const renderList = (props: PaymentProps) => {
  let { paymentMethods, selectChanged, selectedPaymentMethod } = props;
  return (
    <PaymentMethodSelect
      selectedPaymentMethod={selectedPaymentMethod}
      paymentMethods={paymentMethods}
      onChange={(value: number) => selectChanged(value)}
    />
  );
};
