import React from 'react';
import { Button, Input } from 'antd';
import TradexIcon from '../../../ui/TradexIcon';
import styles from './index.module.scss';

type Props = {
  step: number;
  defaultValue: number;
  max: number;
  min: number;
  btnStyle: object;
  inputStyle: object;
  rbtnStyle: object;
  containerStyle: object;
  disabled: boolean;
  check: (num: number) => number;
  onChange: (value: string) => void;
  fromatValue: (value: string) => string;
};

type State = {
  value: string;
};

class InputNumber extends React.Component<Props, State> {
  state: State = {
    value: this.props.defaultValue.toString()
  };

  static defaultProps = {
    step: 1,
    defaultValue: 1,
    max: 99,
    min: 1,
    btnStyle: {
      borderRadius: 0,
      display: 'flex',
      justifyContent: 'center',
      alignItems: 'center',
      borderWidth: '0px',
      width: 31,
      height: 22,
      color: '#001a33'
    },
    inputStyle: {
      borderRadius: 0,
      height: 22,
      textAlign: 'center',
      padding: '5px 0px',
      borderWidth: '0px 1px',
      color: '#3acdfe',
      fontSize: 12
    },
    rbtnStyle: {
      display: 'flex',
      justifyContent: 'center',
      alignItems: 'center',
      borderRadius: 0,
      borderWidth: '0px',
      width: 31,
      height: 22
    },
    containerStyle: {
      display: 'flex',
      width: 114,
      height: 24,
      border: 'solid 1px #ebebeb',
      backgroundColor: '#ffffff'
    },
    check: null,
    disabled: true,
    onChange: (value: string) => {},
    fromatValue: (value: string) => value
  };

  handleChange(value: string) {
    if (value.trim() === '') {
      value = '';
    } else if (isNaN(Number(value))) {
      value = '1';
    }
    value = this.checkValue(Number(value)).toString();
    this.setState({ value });
    this.props.onChange(value);
  }

  add() {
    let { value } = this.state;
    let num = Number(value);
    if (num === 1) {
      num = 5;
    } else {
      num += 5;
    }
    this.setState({ value: num.toString() });
    this.props.onChange(num.toString());
  }

  decrease() {
    let { value } = this.state;
    let num = Number(value);
    num -= 5;
    if (num < this.props.min) {
      num = this.props.min;
    }
    this.setState({ value: num.toString() });
    this.props.onChange(num.toString());
  }

  checkValue(num: number): number {
    if (this.props.check) {
      return this.props.check(num);
    } else {
      return this.defaultCheck(num);
    }
  }

  defaultCheck(num: number) {
    if (num > this.props.max) {
      return this.props.max;
    } else if (num < this.props.min) {
      return this.props.min;
    } else {
      return num;
    }
  }

  render(): React.ReactElement {
    let { min, max, fromatValue, disabled } = this.props;
    let { btnStyle, inputStyle, containerStyle } = this.props;
    let { value } = this.state;
    return (
      <div style={containerStyle}>
        <Button
          className={`${styles.btnStyle} ${styles.rbtn} ${
            Number(value) <= min ? styles.btnDisabled : ''
          }`}
          onClick={() => this.decrease()}
          disabled={Number(value) <= min}
        >
          -
        </Button>
        <Input
          style={inputStyle}
          value={fromatValue(value)}
          onChange={ev => (disabled ? '' : this.handleChange(ev.target.value))}
        />
        <Button
          className={`${styles.btnStyle} ${styles.rbtn} ${
            Number(value) >= max ? styles.btnDisabled : ''
          }`}
          onClick={() => this.add()}
          disabled={Number(value) >= max}
        >
          +{/* <TradexIcon type="iconicon_add_line_thin" className={styles.add} /> */}
        </Button>
      </div>
    );
  }
}

export { InputNumber };
